.sb-btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    outline: none;
    padding: 0px;
    border: 0px;
    font-weight: 600;
    box-shadow: 0px 2px 5px rgba(60, 66, 87, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.08), inset 0px -1px 1px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    transition: all .15s ease-in-out;
}

.sb-btn[data-icon-position="left"]:not([data-onlyicon="false"]) {
    flex-direction: row;
}

.sb-btn[data-full-width="true"] {
    width: 100%;
}

.sb-load-button {
    height: auto;
}

