
.sb-modal-ctn[data-size="lightbox-pro"] .sb-modal-insider {
    width: 964px;
    max-width: calc(100% - 200px);
    height: 80vh !important;
    max-height: 80vh !important;
    background: #fff;
    color: #141b38;
    position: relative;
    box-shadow: 0px 26.7377px 77.2886px rgb(0 0 0 / 11%),
        0px 14.2952px 41.3222px rgb(0 0 0 / 9%),
        0px 8.01379px 23.1649px rgb(0 0 0 / 8%),
        0px 4.25607px 12.3027px rgb(0 0 0 / 6%),
        0px 1.77104px 5.11942px rgb(0 0 0 / 4%);
    overflow-y: hidden;
}

.sb-modal-ctn[data-size="lightbox-pro"] .sbt-lightbox-modal-iframe-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    background-color: black;
}

.sb-modal-insider .sbt-lightbox-modal-wrapper {
    height: 100%;
    display: flex;
}

.sb-modal-insider .sbt-lightbox-modal-wrapper .sbt-lightbox-modal-iframe-wrap {
    flex: 1;
}

.sb-modal-insider .sbt-lightbox-modal-wrapper .sbt-lightbox-modal-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    min-width: 0px !important;
    flex: 1;
}

.sbt-lightbox-modal-header {
    height: 5%;
    max-height: 5%;
}

.sbt-lightbox-modal-caption {
    max-height: 80%;
    overflow: auto;
    scrollbar-width: none;
}

.sbt-lightbox-modal-footer {
    max-height: 15%;
    height: 15%;
    align-items: flex-start;
}
