/* Header */
.sb-feed-header-content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.sb-feed-header-content:not(:has(.sb-feed-header-stats-info)) {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.sb-feed-header-profile {
    display: flex;
    justify-content: space-between;
    gap: 28px;
}

/* sb-feed-header-profile with no sb-feed-header-description and sb-feed-header-stats-info */
.sb-feed-header-profile:not(:has(.sb-feed-header-description)):not(:has(.sb-feed-header-stats-info)) {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 28px;
}

.sb-feed-header-logo.small {
    height: 48px;
    width: 48px;
    border-radius: 50%;
}

.sb-feed-header-logo.medium {
    height: 72px;
    width: 72px;
    border-radius: 50%;
}

.sb-feed-header-logo.large {
    height: 128px;
    width: 128px;
    border-radius: 50%;
}

.sb-feed-header-user-info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 4px;
}

.sb-feed-header-display-name {
    display: flex;
    gap: 8px;
}

.sb-feed-header-stats-info {
    margin-top: 18px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 18px;
}

/* Header Responsive */
@media all and (max-width: 640px) {
    .sb-feed-header-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 24px;
    }

    .sb-feed-header-profile {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 28px;
    }

    .sb-feed-header-stats-info>span {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 4px;
    }

    .sb-feed-header-user-info {
        align-items: center;
    }
}

/* Header Mobile Preview Customizer */
.sb-customizer-preview[data-preview-device="mobile"] .sb-feed-header-content,
.sb-narrow-width .sb-feed-header-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 24px;
}

.sb-customizer-preview[data-preview-device="mobile"] .sb-feed-header-profile,
.sb-narrow-width .sb-feed-header-profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 28px;
}

.sb-customizer-preview[data-preview-device="mobile"] .sb-feed-header-stats-info>span,
.sb-narrow-width .sb-feed-header-stats-info>span {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
}

.sb-customizer-preview[data-preview-device="mobile"] .sb-feed-header-user-info,
.sb-narrow-width .sb-feed-header-user-info {
    align-items: center;
}

.sb-customizer-preview[data-preview-device="mobile"] .sb-feed-header-display-name,
.sb-narrow-width .sb-feed-header-display-name {
    flex-direction: column;
}