/* Grid/Carousel/Masonry Layout */

[data-post-style="regular"] .sb-post-block-item .sb-post-item-author {
  padding: 8px 0px;
}

[data-post-style="boxed"] .sb-post-block-item .sb-post-item-author {
  padding: 8px;
}

/* Common */
.sb-post-item {

  .sb-post-item-author-name {
    font-size: 12px;
    font-weight: 600;
    line-height: 1.2;
    margin: 0;
    color: #141B38;
    word-break: break-all;
  }

  /* image */
  .sb-post-item-image-wrap svg {
    filter: drop-shadow(0 0 1px rgb(0 0 0 / 0.5));
  }

  .sb-post-item-image-wrap:hover img {
    cursor: pointer;
    transform: scale(1.1);
  }

  .sb-post-item-image {
    width: 100%;
    height: inherit;
    object-fit: cover;
    scale: 1;
    transition: transform 500ms ease-in-out;
    vertical-align: middle;
  }

  .sb-post-item-meta-views,
  .sb-post-item-meta-likes {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
  }

  .sb-post-item-play {
    position: absolute;
    bottom: 3rem;
    left: 12px;
    color: white;
  }

  .sb-post-item-play.sb-no-meta {
    bottom: 1rem;
  }
}

/* Not List Layout */
.sb-post-block-item {

  .sb-post-item-author {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  .sb-post-item-author-image {
    width: 24px !important;
    height: 24px;
    border-radius: 50%;
    object-fit: cover;
  }

  .sb-post-item-meta {
    position: absolute;
    bottom: 12px;
    left: 12px;
    color: white;
    display: flex;
    gap: 12px;
    font-size: 12px;
    font-weight: 600
  }

  .sb-post-item-meta-likes,
  .sb-post-item-meta-views {
    text-shadow: 0 0 2px rgb(0 0 0 / 0.5);
  }
}

/* List Layout */
.sb-post-list-item {
  display: flex;

  .sb-post-item-details {
    padding: 20px 16px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
    flex: 1;
  }


  .sb-post-item-author {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 16px;
  }

  .sb-post-item-author-image {
    width: 44px !important;
    height: 44px;
    border-radius: 50%;
    object-fit: cover;
  }

  .sb-post-item-author-name {
    font-size: 13px;
  }

  .sb-post-item-meta {
    display: flex;
    gap: 12px;
    font-size: 12px;
    font-weight: 600;
    color: #2C324C;
  }
}

/* Widget Layout */
:not(.sb-narrow-width)[data-feed-template="widget"][data-layout="grid"] .sb-post-item {
  max-width: 50%;
  margin: auto;
}

/* Gallery Layout */
[data-feed-template="gallery"] .sb-list-wrapper .sb-post-item .sb-post-item-caption {
  padding: 0 !important;
}

/*
	Modals
*/
body.sbt-lightbox-modal-open {
  overflow: hidden;
}

.sb-modal-ctn {
  position: fixed;
  height: 100vh;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 9989;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  overflow-y: auto;
}

.sb-modal-ctn[data-size="lightbox"] .sb-modal-insider {
  width: 964px;
  max-width: calc(100% - 200px);
  max-height: 90vh;
  background: #fff;
  color: #141b38;
  position: relative;
  box-shadow: 0px 26.7377px 77.2886px rgb(0 0 0 / 11%),
    0px 14.2952px 41.3222px rgb(0 0 0 / 9%),
    0px 8.01379px 23.1649px rgb(0 0 0 / 8%),
    0px 4.25607px 12.3027px rgb(0 0 0 / 6%),
    0px 1.77104px 5.11942px rgb(0 0 0 / 4%);
  overflow-y: auto;
}

.sb-modal-ctn[data-size="lightbox-free"] .sb-modal-insider {
  /* width: 964px; */
  width: 500px;
  max-width: 50%;
  max-height: 90vh;
  background: #fff;
  color: #141b38;
  position: relative;
  box-shadow: 0px 26.7377px 77.2886px rgb(0 0 0 / 11%),
    0px 14.2952px 41.3222px rgb(0 0 0 / 9%),
    0px 8.01379px 23.1649px rgb(0 0 0 / 8%),
    0px 4.25607px 12.3027px rgb(0 0 0 / 6%),
    0px 1.77104px 5.11942px rgb(0 0 0 / 4%);
  /* overflow-y: auto; */
  border-radius: 8px;
}

.sb-modal-insider h4 {
  margin-bottom: 4px;
}

.sbt-lightbox-modal-close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  width: 30px;
  text-align: center;
  box-sizing: content-box;
}

.sb-modal-ctn[data-size="lightbox"] .sbt-lightbox-modal-iframe-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: black;
}

.sb-modal-ctn[data-size="lightbox-free"] .sbt-lightbox-modal-iframe-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 758px;
  width: 100%;
  background-color: #F4F4F4;
  padding-top: 40px;
  border-radius: 8px;
}

.sbt-lightbox-modal-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  .sbt-lightbox-modal-content {
    padding: 24px;
    max-width: 330px;
  }

  .sbt-lightbox-modal-caption {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.2;
    letter-spacing: 0.28px;
    margin-bottom: 24px;
    color: #141B38;
  }

}

.sbt-lightbox-modal-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 12px;
  text-decoration: none;
  margin-bottom: 16px;

  .sbt-lightbox-modal-avatar {
    border-radius: 50%;
    object-fit: cover;
    height: 32px;
    width: 32px;
  }

  .sbt-lightbox-modal-username {
    font-size: 14px;
    font-weight: 600;
    line-height: 1.2;
    letter-spacing: 0.28px;
    margin-bottom: 0;
    margin-top: 0;
    color: #141B38;
  }

  .sbt-lightbox-modal-date {
    font-size: 11px;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 0;
    color: #434960;
  }
}

.sbt-lightbox-modal-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
  padding: 8px 0 10px 0;

  .sbt-lightbox-modal-stats {
    display: flex;
    gap: 24px;
    color: #141B38;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 0.24px;
  }

  .sbt-lightbox-modal-views,
  .sbt-lightbox-modal-likes {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
  }
}

.sbt-lightbox-modal-prev {
  position: fixed;
  top: calc(50% - 16px);
  left: 10px;
  z-index: 1000001;
  background-color: lightgrey;
  border-radius: 50%;
  padding: 8px;
  cursor: pointer;
  box-sizing: content-box;
}

.sbt-lightbox-modal-next {
  position: fixed;
  top: calc(50% - 16px);
  right: 10px;
  z-index: 1000001;
  background-color: lightgrey;
  border-radius: 50%;
  padding: 8px;
  cursor: pointer;
  box-sizing: content-box;
}

/*
    Owl Carousel Styling
*/
[data-layout="carousel"] .owl-nav button {
  position: absolute;
  width: 30px;
  height: 30px;
  top: calc(50% - 15px);
  left: 0;
  display: inline-flex;
  opacity: 0.7;
  border-radius: 50%;
  background: #fff !important;
  color: #000 !important;
  box-shadow: 0 0 5px 0 rgb(0 0 0 / 20%);
  overflow: hidden;
  line-height: 99999;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

[data-layout="carousel"] .owl-nav button:before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  top: 10px;
  left: 11px;
  border-left: 2px solid currentColor;
  border-bottom: 2px solid currentColor;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

[data-layout="carousel"] .owl-nav .owl-next {
  left: unset;
  right: 0;
}

[data-layout="carousel"] .owl-nav .owl-next:before {
  left: 7px;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

[data-layout="carousel"] .owl-nav button:hover {
  opacity: 1;
}

.owl-nav button span {
  display: none !important;
}

[data-layout="carousel"] .owl-dots {
  position: relative;
  text-align: center;
  padding-right: 4px;
}

[data-layout="carousel"] .owl-dot {
  width: 10px;
  height: 10px;
  background: #e6e6eb !important;
  border-radius: 50px;
  margin: 0 4px;
  display: inline-block;
}

[data-layout="carousel"] .owl-dot.active {
  background: #d0d0d8 !important;
}

/* Grid columns */
.sb-grid-wrapper[data-grid-columns="1"] {
  grid-template-columns: repeat(1, 1fr);
}

.sb-grid-wrapper[data-grid-columns="2"] {
  grid-template-columns: repeat(2, 1fr);
}

.sb-grid-wrapper[data-grid-columns="3"] {
  grid-template-columns: repeat(3, 1fr);
}

.sb-grid-wrapper[data-grid-columns="4"] {
  grid-template-columns: repeat(4, 1fr);
}

.sb-grid-wrapper[data-grid-columns="5"] {
  grid-template-columns: repeat(5, 1fr);
}

.sb-grid-wrapper[data-grid-columns="6"] {
  grid-template-columns: repeat(6, 1fr);
}

/* Posts grid responsive */
@media all and (max-width: 480px) {
  .sb-grid-wrapper[data-grid-mobile-columns="1"] {
    grid-template-columns: repeat(1, 1fr);
  }

  .sb-grid-wrapper[data-grid-mobile-columns="2"] {
    grid-template-columns: repeat(2, 1fr);
  }

  .sb-grid-wrapper[data-grid-mobile-columns="3"] {
    grid-template-columns: repeat(3, 1fr);
  }

  .sb-grid-wrapper[data-grid-mobile-columns="4"] {
    grid-template-columns: repeat(4, 1fr);
  }

  .sb-grid-wrapper[data-grid-mobile-columns="5"] {
    grid-template-columns: repeat(5, 1fr);
  }

  .sb-grid-wrapper[data-grid-mobile-columns="6"] {
    grid-template-columns: repeat(6, 1fr);
  }

  .sb-post-list-item {
    flex-direction: column;

    .sb-post-item-image-wrap {
      width: 100% !important;
    }
  }
}

@media all and (min-width: 481px) and (max-width: 640px) {
  .sb-grid-wrapper[data-grid-tablet-columns="1"] {
    grid-template-columns: repeat(1, 1fr);
  }

  .sb-grid-wrapper[data-grid-tablet-columns="2"] {
    grid-template-columns: repeat(2, 1fr);
  }

  .sb-grid-wrapper[data-grid-tablet-columns="3"] {
    grid-template-columns: repeat(3, 1fr);
  }

  .sb-grid-wrapper[data-grid-tablet-columns="4"] {
    grid-template-columns: repeat(4, 1fr);
  }

  .sb-grid-wrapper[data-grid-tablet-columns="5"] {
    grid-template-columns: repeat(5, 1fr);
  }

  .sb-grid-wrapper[data-grid-tablet-columns="6"] {
    grid-template-columns: repeat(6, 1fr);
  }

  .sb-post-list-item {
    flex-direction: column;

    .sb-post-item-image-wrap {
      width: 100% !important;
    }
  }
}

@media all and (max-width: 480px) {
  .sbt-lightbox-modal .sb-modal-insider {
    overflow-x: hidden;
    max-width: calc(100% - 65px) !important;
  }

  .sbt-lightbox-modal-wrapper {
    flex-direction: column !important;
  }

  .sbt-lightbox-modal-iframe-wrap {
    justify-content: flex-start !important;
    padding-top: 0px !important;
  }

  .sbt-lightbox-modal-close {
    position: fixed;
    top: 6px;
    right: 2px;
    background: lightgray;
    border-radius: 50%;
    width: 18px !important;
    height: 18px !important;
    padding: 6px !important;
  }

  .sbt-lightbox-modal-close svg {
    width: 18px !important;
    height: 18px !important;
  }

  .sbt-lightbox-modal-next {
    right: 2px !important;
    padding: 6px !important;
    width: 16px !important;
    height: 16px !important;
  }

  .sbt-lightbox-modal-prev {
    left: 2px !important;
    padding: 6px !important;
    width: 16px !important;
    height: 16px !important;
  }
}

@media all and (min-width: 481px) and (max-width: 768px) {
  .sbt-lightbox-modal .sb-modal-insider {
    overflow-x: hidden;
    max-width: calc(100% - 130px) !important;
  }

  .sbt-lightbox-modal-content {
    min-width: 230px !important;
  }

  /*.sbt-lightbox-modal-wrapper {
    min-width: 230px !important;
  } */
}

@media all and (min-width: 769px) {
  .sbt-lightbox-modal-content {
    min-width: 330px !important;
  }

  .sbt-lightbox-modal-wrapper {
    min-width: 330px !important;
  }
}

/* Post Mobile Preview Customizer */
.sb-customizer-preview[data-preview-device="mobile"] .sb-post-list-item,
.sb-narrow-width .sb-post-list-item {
